@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700);
html,body{margin:0;height:100%;width:100%;font-size:14px;font-family:"Helvetica Neue", Helvetica, Arial, Sans-serif, serif;font-weight:300;text-align:center;cursor:default;transition:background-color 0.5s ease-in, box-shadow 0.5s ease-in;-webkit-user-select:none;-moz-user-select:none;-o-user-select:none;-ms-user-select:none;user-select:none;-ms-touch-action:none}div{cursor:inherit}.center{position:fixed;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center}.center .dot{position:absolute;justify-content:center;width:512px;height:512px}.center .image-icon{position:absolute;display:flex;top:3%;right:5%}.center .image-icon .download{padding-left:25px}

.react-cropper-modal{position:fixed;background-color:rgba(0,0,0,0.3);top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center}.react-cropper-modal .modal-panel{margin-bottom:100px;width:880px;height:500px;background:white;padding:20px;display:flex;flex-direction:column;align-items:stretch}.react-cropper-modal .modal-panel .cropper-container-container{flex:1 1;display:flex;align-items:stretch;justify-content:space-between;height:100%}.react-cropper-modal .modal-panel .cropper-container-container .cropper-container .cropper{width:100%;height:100%}.react-cropper-modal .modal-panel .cropper-container-container .cropper-container .cropper .cropper-bg{background-image:none}.react-cropper-modal .button-row{position:absolute;margin-top:280px;display:flex;justify-content:center}

.upload .upload-input-label .base-upload-input{display:none}

.error-container{position:fixed;top:0;bottom:0;left:0;right:0;display:flex;justify-content:center;align-items:center}.error-container .error-box{width:300px;height:300px;font-family:"Amatic SC",cursive;text-transform:uppercase;position:absolute;background:linear-gradient(to bottom left, #EF8D9C 40%, #FFC39E 100%);border-radius:20px;box-shadow:5px 5px 20px #cbcdd3}.error-container .error-box h1{font-size:2em;font-weight:100;letter-spacing:3px;padding-top:5px;color:#FCFCFC;padding-bottom:5px;text-transform:uppercase}.error-container .error-box .red{color:#e96075}.error-container .error-box .alert{font-weight:700;letter-spacing:5px}.error-container .error-box p{margin-top:-5px;font-size:1.5em;font-weight:100;color:#5e5e5e;letter-spacing:1px}.error-container .error-box button,.error-container .error-box .dot{cursor:pointer}.error-container .error-box .dot{width:25px;height:25px;background:#ffe411;border-radius:50%;position:absolute;top:4%;right:6%}.error-container .error-box .dot:hover{background:#FCFCFC}.error-container .error-box .face{position:absolute;width:18%;height:18%;background:#FCFCFC;border-radius:50%;border:1px solid #777;top:21%;left:37.5%;z-index:2;-webkit-animation:roll 3s ease-in-out infinite;animation:roll 3s ease-in-out infinite}.error-container .error-box .eye{position:absolute;width:3px;height:3px;background:#777;border-radius:50%;top:40%;left:20%}.error-container .error-box .right{left:68%}.error-container .error-box .mouth{position:absolute;top:43%;left:41%;width:5px;height:5px;border-radius:50%}.error-container .error-box .happy{border:2px solid;border-color:transparent #777 #777 transparent;transform:rotate(45deg)}.error-container .error-box .sad{top:49%;border:2px solid;border-color:#777 transparent transparent #777;transform:rotate(45deg)}.error-container .error-box .shadow{position:absolute;width:21%;height:3%;opacity:.5;background:#777;left:40%;top:43%;border-radius:50%;z-index:1}.error-container .error-box .scale{-webkit-animation:scale 1s ease-in infinite;animation:scale 1s ease-in infinite}.error-container .error-box .move{-webkit-animation:move 3s ease-in-out infinite;animation:move 3s ease-in-out infinite}.error-container .error-box .message{position:absolute;width:100%;text-align:center;height:40%;top:47%}@-webkit-keyframes bounce{50%{transform:translateY(-10px)}}@keyframes bounce{50%{transform:translateY(-10px)}}@-webkit-keyframes scale{50%{transform:scale(0.9)}}@keyframes scale{50%{transform:scale(0.9)}}@-webkit-keyframes roll{0%{transform:rotate(0deg);left:25%}50%{left:60%;transform:rotate(168deg)}100%{transform:rotate(0deg);left:25%}}@keyframes roll{0%{transform:rotate(0deg);left:25%}50%{left:60%;transform:rotate(168deg)}100%{transform:rotate(0deg);left:25%}}@-webkit-keyframes move{0%{left:25%}50%{left:60%}100%{left:25%}}@keyframes move{0%{left:25%}50%{left:60%}100%{left:25%}}footer{position:absolute;bottom:0;right:0;text-align:center;font-size:0.5em;text-transform:uppercase;padding:10px;font-family:"Amatic SC",cursive}footer p{color:#EF8D9C;letter-spacing:2px}footer a{color:#B0DB7D;text-decoration:none}footer a:hover{color:#FFC39E}

.bulb-container{position:absolute;top:0;left:5%;transition:background-color 1.5s ease-in, box-shadow 1.5s ease-in}.bulb-container .light{-webkit-animation:swing 5s ease-in-out infinite;animation:swing 5s ease-in-out infinite}.bulb-container .cable{width:3px;height:50px;margin:0 auto;background:rgba(0,0,0,0.8);position:relative}.bulb-container .bulb-cover{margin:8px auto;margin-top:0;width:10px;height:20px;background:rgba(0,0,0,0.8);border-radius:30% 30% 0 0}.bulb-container .bulb{position:relative;margin:-11px auto;width:30px;height:45px;border-radius:45%;border:1px rgba(0,0,0,0.3);box-shadow:inset 0 0 2px 2px rgba(0,0,0,0.3)}.bulb-container .bulb-light{content:"";position:absolute;left:9px;right:15px;top:2px;width:10px;height:20px;border-radius:40% 40% 60% 60%;border:1.5px solid rgba(0,0,0,0.1);box-shadow:0 0 0.5px rgba(255,255,255,0.1)}@-webkit-keyframes swing{0%{transform:rotate(1.5deg)}50%{transform:rotate(-1.5deg)}100%{transform:rotate(1.5deg)}}@keyframes swing{0%{transform:rotate(1.5deg)}50%{transform:rotate(-1.5deg)}100%{transform:rotate(1.5deg)}}

